import Toast from '../../wxcomponents/vant/toast/toast.js';
import Dialog from '../../wxcomponents/vant/dialog/dialog.js';
export default {
	data() {
		return {
			// 是否登录
			isLogin: false,
			// 用户微信信息
			userDetailWx: {},
			// 分享上拉菜单
			isShowActionSheet: false,
			// 上拉菜单的内容
			actions: [{
				name: '分享',
				openType: 'share'
			}]
		}
	},
	onLoad: function() {
		this.isLogin = uni.getStorageSync('isLogin')
		if (uni.getStorageSync('isLogin')) {
			this.userDetailWx.nickName = uni.getStorageSync('userInfo').userName
			this.userDetailWx.avatarUrl = uni.getStorageSync('userInfo').avatar
		}
	},
	onShow: function() {
		this.isLogin = uni.getStorageSync('isLogin')
		if (uni.getStorageSync('isLogin')) {
			this.userDetailWx.nickName = uni.getStorageSync('userInfo').userName
			this.userDetailWx.avatarUrl = uni.getStorageSync('userInfo').avatar
		}
	},
	methods: {
		// 点击跳转登录页面
		toLogin: function() {
			uni.navigateTo({
				url: '../login/index'
			})
		},
		// 跳转地址管理页面
		toAddressList: function() {
			uni.navigateTo({
				url: '../addressList/index'
			})
		},
		// 跳转订单列表
		toOrederList: function(index) {
			if (this.isLogin) {
				if (uni.getStorageSync('selectShopDetail')) {
					// 0:全部 1:未支付 2:已支付 3:已发货 4:已退货
					uni.navigateTo({
						url: '../orderList/index?active=' + index
					})
				} else {
					Dialog.alert({
						title: '提示',
						message: '请先选择商铺'
					}).then(() => {
						uni.switchTab({
							url: '../index/index'
						})
					})
				}
			} else {
				Toast('请先登录')
			}
		},
		// 点击优惠券等
		showToast: function() {
			Toast('敬请期待')
		}
	}
}
